import { Component, OnInit } from '@angular/core';
import { IonItem, ModalController, PopoverController } from '@ionic/angular';
import { OpcionesCDOComponent } from 'src/app/components/opciones-cdo/opciones-cdo.component';
import { ApiService } from 'src/app/Services/Api/Api.service';
import * as _ from 'underscore'
import * as moment from 'moment-timezone'
import { ActivatedRoute } from '@angular/router';
import { ControlcdoasistencialComponent } from 'src/app/components/controlcdoasistencial/controlcdoasistencial.component';
import { AssertionError } from 'assert';


@Component({
  selector: 'app-cdosupervisores',
  templateUrl: './cdosupervisores.page.html',
  styleUrls: ['./cdosupervisores.page.scss'],
})
export class CdosupervisoresPage implements OnInit {

  actividades = [];
  unicos = [];

  filtro = 'Todos'
  txt = '';

  cargaActividades;
  tkn;

  padres = [];

  constructor(
    private api: ApiService,
    private popover: PopoverController,
    private route: ActivatedRoute,
    private modalCtrl: ModalController
  ) { }

  ngOnInit() {
  }

  getInfo(guid) {
    return new Promise((resolve, reject) => {
      this.api.getActivitieInfo(this.tkn, guid).subscribe((data) => {
        if (data) {
          resolve(data)
        } else {
          resolve (false)
        }
      })
    })
  }


  ionViewWillEnter() {

    this.route.queryParams.subscribe((param) => {
      console.log(param);
      this.tkn = param.tkn;
      this.cargarData()
    })

  
   
  }

  cargarData() {
    this.cargaActividades = false;
    this.actividades = [];
    this.api.getActivities(this.tkn).subscribe(async (data: any) => {
     

      console.log(data)
    

      

     // data = data.filter((item) => item.CompanyStatusName != 'Borrado' && item.ParentGUID)
     data = data.filter((item) => item.ParentGUID)

      this.unicos = _.uniq(_.map(data, (item) => item.CompanyStatusName)) // .filter((item) => item != 'Borrado')
      this.unicos.unshift('Todos')

      this.padres = _.uniq(_.map(data, (item) => item.ParentGUID))

      const arr = [];

      for (const padre of this.padres) {
        const dt: any = await this.getInfo(padre);
      

        let createdon = moment(dt.CreatedOn).format('YYYY-MM-DD');
        let actual = moment().format('YYYY-MM-DD');

        if (createdon == actual) {
          var inicio = moment(dt.CreatedOn).format('YYYY-MM-DD HH:mm:ss')
          var fin = moment().format('YYYY-MM-DD HH:mm:ss')
          var dur = moment(fin).diff(moment(inicio), 'seconds');
          var loadDuration = false;
          var finDuration = '';
          var aseoInfo = dt.Values.filter((item) => {
            return item.apiId == 'TIPO_DE_ASEO'
          })
          var aseo = '';

          if (aseoInfo.length > 0) {
            aseo = aseoInfo[0].Value
          }

          let fec = '';
          const dataParent = data.filter((it) => it.ParentGUID == padre)

          const estados = _.uniq(_.map(dataParent, (item) => item.CompanyStatusName))
          const arrTemp = []
          for (const status of estados) {
            const dataStatus = dataParent.filter((it) => it.CompanyStatusName == status)
            const childs = [];
            for (const item of dataStatus) {

           

              const miData: any = await this.getInfo(item.GUID)

               if (status == 'Completado') {
                  var gpsfin = miData.Values.filter((item) => {
                    return item.apiId == 'FECHAHORAFINACTIVIDAD'
                  })

                  if (gpsfin.length > 0) {
                    var dataGPSFin = JSON.parse(gpsfin[0].Value)
                    var time = dataGPSFin.filter((item) => item.apiId == 'time');

                    if (time.length > 0) {
                      console.log(time[0].Value, 'TIME', dataGPSFin)
                      finDuration = moment(parseFloat(time[0].Value)).format('YYYY-MM-DD HH:mm');
                      loadDuration = true;
                    }
                  
                  }

                  
                }

              moment.locale('es')
              
      
              if (miData) {
                miData.color = this.color(miData.CompanyStatusName);
                miData.icolor = this.color(miData.CompanyStatusName, true);
      
                if (miData.Values.length) {
                  const fecha = miData.Values.filter((dat) => dat.apiId == 'FECHA_SOLICITUD');
                  if (fecha.length > 0) {
                    fec = moment(fecha[0].Value).format('dddd, DD MMMM YYYY hh:mm:a');
                    const mifecha = moment(fecha[0].Value).format('YYYY-MM-DD HH:mm');
                    const duracion = moment(moment().format('YYYY-MM-DD HH:mm')).diff(moment(mifecha), 'minutes')
                    miData.tiempo = duracion + ' min'
                  } else {
                    miData.tiempo = '---';
                  }
                } else {
                  miData.tiempo = '---';
                }
            
               //  this.actividades.push(miData);
      
               childs.push(miData)
              }
              
      
            }

            arrTemp.push({
              color: this.color(status),
              iconColor: this.color(status, true),
              status: status,
              childs: childs
            })
  
          }

        // guardamos hijos

        var durCompleted = 0;

        if (loadDuration) {
          durCompleted = moment(finDuration).diff(moment(inicio), 'minutes');
        }

      
          this.actividades.push({
            loc: dataParent[0].LocationName,
            ass: dataParent[0].AssetName,
            fec: fec,
            data: arrTemp,
            tiempo: dur,
            loadDuration,
            fin: finDuration,
            aseo: aseo,
            dur: durCompleted
          })

          if (loadDuration) {
         
          }

         // this.cargaActividades = true;
       //  return;
      
        }


     
     

      }


      console.log(arr);
 


      this.cargaActividades = true;

      console.log(this.actividades)

   
   
    })
  }

  cambio(event) {
    this.txt = event.detail.value;
  }

  async handleRefresh(event) {

    await this.cargarData();
    this.filtro = 'Todos'
    event.target.complete();
  }

  color(name, ic?) {
    if (name == 'CANCELADO POR DEMORA') {
      return ic ? 'igris' : 'gris'
    }
    if (name == 'Completado') {
      return ic ? 'iverde' :  'verde'
    }

    if (name == 'EN PROCESO DE ASEO') {
      return ic ? 'iamarillo' :  'amarillo'
    }

    if (name == 'PENDIENTE ASEO') {
      return ic ? 'irojo' :  'rojo'
    }

    if (name == 'Borrado') {
      return ic ? 'igris' :  'gris'
    }

    if (name == 'ELIMINADA POR LIMPIEZA PREVIA A SOLICITUD') {
      return ic ? 'iazul' :  'azul'
    }

    
  }

  async filtros(event ) {

    const miPopover = await this.popover.create({
      component: OpcionesCDOComponent,
      componentProps: {
        opciones: this.unicos
      },
      event: event
    })

    await miPopover.present();

    const dataResume: any = await miPopover.onWillDismiss();

    if (dataResume.data) {
      this.filtro = dataResume.data.by;
    }

  }

  async control(event ) {

    const modal = await this.modalCtrl.create({
      component: ControlcdoasistencialComponent,
      componentProps: {
        padres: this.padres,
        data: this.actividades,
        tkn: this.tkn
      }
  
    })

    await modal.present();

    const resp = await modal.onWillDismiss();

    if (resp.data) {

      if (resp.data.revisados.length > 0) {
        _.each(resp.data.revisados, (ele, i) => {
          _.each(this.actividades, (item, j) => {
            if (item.ParentGUID == ele) {
              item.CompanyStatusName = 'ELIMINADA POR LIMPIEZA PREVIA A SOLICITUD'
              item.color = this.color(item.CompanyStatusName);
              item.icolor = this.color(item.CompanyStatusName, true);
            }
          })
        })
      }

    } 

  

  }

}
